.dropdown{position:relative;display:inline-block}.dropdown.open>.toggle{-webkit-box-shadow:inset 0 3px 5px rgba(0, 0, 0, .125);box-shadow:inset 0 3px 5px rgba(0, 0, 0, .125);outline:none;background-color:#e8ecf3}.toggle{background:transparent}.toggle:hover{background-color:#e8ecf3}.Menu{position:absolute;top:100%;left:0;z-index:1030;min-width:160px;padding:8px 0;margin:7px 0;background:#fff;border-radius:4px;-webkit-box-shadow:0 2px 6px rgba(0, 0, 0, .35);box-shadow:0 2px 6px rgba(0, 0, 0, .35);list-style:none;text-align:left;color:#111;font-size:1.3rem;line-height:1.5}.MenuTop{top:auto;bottom:100%}.MenuRight{left:auto;right:0}.item>a, .item>button, .item>span{padding:8px 15px;display:block;width:100%;color:#111;border-radius:0;border:0;background:none;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;-webkit-box-shadow:none;box-shadow:none;text-align:left;font-size:1.3rem;font-weight:normal;text-decoration:none}.item>a:hover, .item>button:hover, .item>span:hover{background:#e8ecf3}.item>a:hover{text-decoration:none}.item.hasIcon>a, .item.hasIcon>button, .item.hasIcon>span{padding-left:40px}.item.hasIcon i{float:left;margin-left:-25px;margin-top:3px;font-size:1.4rem}.divider{margin:8px 0;background-color:#e8ecf3;height:1px}


