.sideNav{position:relative}@media (min-width:992px){.sideNav{width:160px}.sideNav>ul{width:100%;margin-top:30px}.sideNav>ul>li{margin-bottom:10px}}.newTopic{margin-bottom:20px}.newTopic .newTopicBtn{display:block;width:100%;overflow:hidden;text-overflow:ellipsis}.newTopic .newTopicBtn>i{margin-right:8px}.itemNav{position:relative}.dropdown{position:relative;display:block}.dropdown>.menu{display:block;border:0;width:auto;margin:0;padding:0;min-width:0;float:none;position:static;background:none;-webkit-box-shadow:none;box-shadow:none}.dropdown>.menu>li>a{display:block;padding:8px 0 8px 30px;color:#667c99}.dropdown>.menu>li>a:hover{text-decoration:none}.dropdown>.menu>li>a>i, .dropdown>.menu>li>a>.btn-icon{float:left;margin-left:-30px;margin-top:1px;margin-right:5px;font-size:1.5rem}.dropdown>.menu>li.active>a{background:none;color:#4d698e;font-weight:bold}.dropdown>.menu>li.divider{margin:8px 0;height:1px;background-color:#e8ecf3}@media (min-width:768px){.menu>li.divider{background:none}}.channelIcon{border-radius:4px;width:16px;height:16px;display:inline-block;vertical-align:-3px;background:#e8ecf3;float:left;margin-left:-30px;margin-top:1px;font-size:1.5rem}


