.appHeader{padding:8px;height:52px;position:relative;top:0;left:0;right:0;z-index:1000;background:rgba(255, 255, 255, .98)}.container{-webkit-transition:width .2s;transition:width .2s}.headerTitle{float:left;vertical-align:top;font-size:18px;font-weight:normal;margin:0 15px 0 0;line-height:34px}.headerPrimary{float:left}.headerControls{margin:0;padding:0;list-style:none;display:inline-block;vertical-align:middle}.headerControls>li{display:inline-block;vertical-align:middle}.btnLink>i{margin-right:5px}.btnLink:hover{color:#426799}.headerSecondary{float:right}.search{position:relative;margin-right:10px;-webkit-transition:margin-left .4s;transition:margin-left .4s}.searchInput{overflow:hidden;color:#667c99}.searchInput input{float:left;width:225px;padding-left:32px;padding-right:32px;-webkit-transition:all .4s;transition:all .4s}.btnLabel{-webkit-transition:margin-right .1s;transition:margin-right .1s}


